@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,200;1,300;1,400;1,500;1,600&display=swap');
.mainP {
    transform: scale(1.27, 1.3);
    margin-top: 22%;
}
.smallImg {
    width: 50px;
    margin: 0 8px;
    cursor: pointer;
    border-radius: 5px;
    padding: 4px;
}
.mysection {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 2% 8%;
    box-sizing: border-box;
    margin: 5%;
}
.mysection > div:nth-of-type(1){
    width: 35%;
    padding-right: 8%;
    box-sizing: border-box;
    align-self: flex-start;
}
.mysection > div:nth-of-type(2){
    align-self: flex-start;
    width: 60%;
    box-sizing: border-box;
    z-index: 5;
}
.mysection > div:nth-of-type(2) > div:nth-of-type(1){
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    /* margin-bottom: 40px; */
}
h3 {
    font-family: "Montserrat", sans-serif;
    margin: 0;
    padding: 0;
    margin-top: 36px;
    font-weight: 200;
    font-size: 20px;
    letter-spacing: 1px;
    color: rgb(27, 25, 25);

}
.mysection>div:nth-of-type(2) h1 {
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-size: 40px;
    letter-spacing: -2px;
    margin: 0;
    padding: 0;
    letter-spacing: 0px;
}
.mysection>div:nth-of-type(2)>div>h4 {
    font-weight: 200;
    font-size: 20px;
    letter-spacing: 1px;
    color: rgb(27, 25, 25);
    margin: 0;
    margin-top: 8px;
    font-family: "Montserrat", sans-serif;
}
.mysection>div:nth-of-type(2)>div>div>p {
    font-size: 16px;
    font-weight: 300;
    color: #86939e;
}
h3 span:nth-of-type(2) {
    font-size: 20px;
    letter-spacing: 1px;
    color: #009688;
    font-weight: 500;
}
.mysection > div:nth-of-type(2) > div:nth-of-type(1) > div:nth-of-type(2) div{
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    margin-top: 20px;
}
.mainP {
    width: 100%;
    box-sizing: border-box;
}
#btn-add-to-cart {
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    padding: 10px 20px;
    letter-spacing: 2px;
    background-color: #009688;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0 2px 10px rgb(0 0 0 / 50%);
    margin: 0;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
    margin-top: 5%;
}

.borderBox{
    border: 2px solid green;
}