}
.top-bar {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 2px 4px 2px rgba(17, 16, 16, 0.5);
    padding: 1% 0;
    position: fixed;
    top: 0;
    z-index: 50;
    background-color: #fff;
  }
  
  /* LOGO STYLE */
  .logo a {
    text-decoration: none;
    color: #000;
  }
  .logo a h4 {
    font-size: 30px;
    font-weight: 100;
    letter-spacing: 3px;
    cursor: pointer;
  }
  .logo a h4 span {
    color: #009688;
    letter-spacing: 1px;
    font-weight: 1000;
  }
  
  /* NAVBAR STYLE */
  .nav-links {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 30%;
  }
  .nav-links li a {
    text-decoration: none;
    font-size: 14px;
    color: rgb(27, 25, 25);
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 2px;
    transition: all 0.2s;
  }
  .nav-links li a:hover {
    color: #009688;
  }
  
  /* ICONS STYLE */
  .icons {
    width:20%;
    /* background-color: #009688; */
    display: flex;
    color: rgb(27, 25, 25);
    justify-content:space-evenly;
    align-items: center;
    
  }
  .icons i {
    font-size: 22px;
    cursor: pointer;
  
  }
  .icons .cart a {
    color: black;
  }
  .cart-count {
    color: #fff;
    background-color: #009688;
    border-radius: 15px;
    font-size: 12px;
    padding: 3% 15%;
  }
  
  /* BURGER STYLE */
  .burger {
    display: none;
    padding-top: 5px;
    cursor: pointer;
  }
  .burger div {
    width: 25px;
    height: 3px;
    background-color: rgb(27, 25, 25);
    margin: 5px;
    transition: all 0.3s ease;
  }
  
  /* BURGER STYLE */
  .toggle .line1 {
    -webkit-transform: rotate(-45deg) translate(-5px, 6px);
            transform: rotate(-45deg) translate(-5px, 6px);
  }
  .toggle .line2 {
    opacity: 0;
  }
  .toggle .line3 {
    -webkit-transform: rotate(45deg) translate(-5px, -6px);
            transform: rotate(45deg) translate(-5px, -6px);
  }
  
  /* NAVBAR RESPONSIVE STYLE */
  @media screen and (max-width: 1024px) {
    body {
      overflow-x: hidden;
    }
    .nav-links {
      position: absolute;
      right: 0;
      height: 89vh;
      /* top: 10.4vh; */
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      width: 50%;
      -webkit-transform: translateX(100%);
              transform: translateX(100%);
      transition: 0.5s ease-in;
      background: #009688;
      z-index: 100;
    }
    .nav-links li {
      opacity: 1;
      margin-top: 100px;
    }
    .nav-links li a {
      color: rgb(27, 25, 25);
    }
    .nav-links li a:hover {
      color: #fff;
    }
    .burger {
      display: block;
    }
  }
  @media screen and (max-width: 425px) {
    body {
      overflow-x: hidden;
    }
    .logo a h4 {
      font-size: 24px;
    }
    .icons i {
      padding-top: 6px;
      font-size: 20px;
      margin-right: 20px;
    }
    .icons .cart {
      margin-right: 15px;
    }
    .burger {
      padding-top: 2px;
    }
  }
  @media screen and (max-width: 320px) {
    .logo h4 {
      font-size: 20px;
    }
    .icons i {
      padding-top: 4px;
      font-size: 16px;
      margin-right: 15px;
    }
    .burger {
      padding-top: 0px;
    }
  }
  
  /* NAVBAR ACTIVE STYLE */
  .nav-active {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  
  /* ============== NAVBAR STYLE END ================== */
  
